<template>
    <div class="postCar">
        <p class="title">刊登車輛資訊</p>
        <p class="sub-title">POST</p>
        <div class="info-input">
            <ul class="info-input-1">
                <li>
                    <p class="input-title">品牌</p>
                    <el-input v-model="brand" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">車款</p>
                    <el-input v-model="car" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">領牌時間</p>
                    <el-date-picker
                        v-model="license"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </li>
                <li>
                    <p class="input-title">車門乘車數</p>
                    <el-input v-model="door" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">驅動方式</p>
                    <el-input v-model="drive" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">車系</p>
                    <el-input v-model="series" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">里程</p>
                    <el-input v-model="mileage" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">顏色</p>
                    <div class="input-box">
                        <el-color-picker v-model="color1"></el-color-picker>
                    </div>
                </li>
                <li>
                    <p class="input-title">燃料</p>
                    <el-input v-model="fuel" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title ">是否公開</p>
                    <div class="input-box">
                        <el-switch
                        v-model=" open "
                        active-color="#ef6d38"
                        inactive-color="#333333">
                        </el-switch><span v-text="open ? '啟用':'未啟用'" />
                    </div>
                </li>
            </ul>
            <p class="input-title left">其他配備</p>
            <el-input
                type="textarea"
                :rows="2"
                :autosize="{ minRows: 5, maxRows: 10}"
                placeholder="请输入内容"
                v-model="textarea">
            </el-input>
            <p class="input-title left">上傳車輛照片</p>
            <div class="upload-img">
                <el-upload
                    class="upload-demo "
                    drag
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :limit="5"
                    multiple>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">將文件拖到此處，或<em>點擊上傳</em></div>
                    <div class="el-upload__tip" slot="tip">最多只能上傳5張jpg或png圖片，且不超過500kb</div>
                </el-upload>
            </div>                   
        </div>
        <div class="car-btn">確認送出</div>
    </div>
</template>

<script>
import VueCropper from 'vue-cropperjs';
export default {
    name:'postCar',
    components: {
        VueCropper,
    },
    data() {
        return {
            brand:'',
            car:'',
            license:'',
            door:'',
            drive:'',
            series:'',
            mileage:'',
            fuel:'',
            textarea:'',
            open: true,
            color1: null,
        }
    }


}
</script>

<style lang='scss' scoped>
.postCar {
    padding: 100px 0;
    text-align: center;
    max-width: 850px;
    width: 80%;
    margin: 0 auto;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
    }
    .info-input {
        margin-top: 50px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .input-title {
            color: #ef6d38;
            padding-left: 25px;
            margin-bottom: 10px;
            text-align: left;
        }
        .left {
            width: 100%;
            margin-top: 35px;
        }
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            li {
                margin-top: 30px;
                padding:0 10px;
            }
        }
        
    }
    .input-box {
        width: 400px;
        height: 60px;
        border-radius: 50px;
        border: 1px solid #DCDFE6;
        align-items: center;
        display: flex;
        padding-left: 25px;
        box-sizing: border-box;
        span {
            color: #999999;
            margin-left: 10px;
        }
        @media screen and (max-width: 768px) {
            width: 300px;
        }
    }
    .upload-img {
        width: 97.5%;
        background: #f5f5f5;
        border: 1px solid #DCDFE6;
        text-align: center;
        padding: 20px 0;
        line-height: 30px;
        border-radius: 30px;
        >>>.el-upload-dragger{
            background-color: rgba(255,255,255,.5);
            em{
                color: #ef6d38;
            }
            &:hover,&:active{
                border-color: #ef6d38 !important;
            }
        }
        .el-upload__tip{
            color: #ef6d38;
            font-size: 15px;
        }
        @media screen and (max-width: 1330px) {
            max-width: 400px;
        }
    }
}

    @media screen and (max-width: 768px) {
        .el-textarea {
            width: 300px;
        }
        .postCar .info-input ul {
            display: block;
            width: 100%;
            .input-box{
                width: 100%;
            }
        }
        .left {
            max-width: 275px;
        }
        .input-title.left{
            max-width: none;
        }
        .el-textarea{
            width: 97.5%;
            max-width: none;
        }
        .postCar .upload-img ,.car-btn {
            max-width: 300px;
        }
        .postCar .upload-img{
            max-width: none;
        }

    }
</style>


